<template>
	<view class="container">
		<!-- Êä•ÂëäÂ§¥ÈÉ® -->
		<view class="report-header">
			<view class="header-icon">
				<text class="icon-text">üìä</text>
			</view>
			<text class="report-title">ÂèëËÇ≤ËØÑ‰º∞Êä•Âëä</text>
			<text class="report-subtitle">{{ childInfo.name }} ÁöÑÁªºÂêàÂèëËÇ≤ËØÑ‰º∞ÁªìÊûú</text>
		</view>
		
		<!-- ÊÄª‰ΩìËØÑÂàÜ -->
		<view class="score-card">
			<view class="score-circle">
				<text class="score-number">{{ assessmentResult.scorePercent }}</text>
				<text class="score-unit">ÂàÜ</text>
			</view>
			<view class="score-info">
				<text class="score-title">{{ getScoreLevel(assessmentResult.scorePercent) }}</text>
				<text class="score-desc">{{ getScoreDescription(assessmentResult.scorePercent) }}</text>
			</view>
		</view>
		
		<!-- ËØ¶ÁªÜÂàÜÊûê -->
		<view class="analysis-section">
			<view class="section-title">
				<text class="title-text">ËØ¶ÁªÜÂàÜÊûê</text>
			</view>
			
			<!-- ÂêÑÁª¥Â∫¶ÂæóÂàÜ -->
			<view class="dimensions-container">
				<view 
					class="dimension-item" 
					v-for="(dimension, index) in dimensionScores" 
					:key="index"
				>
					<view class="dimension-header">
						<text class="dimension-name">{{ dimension.name }}</text>
						<text class="dimension-score">{{ dimension.score }}/{{ dimension.maxScore }}</text>
					</view>
					<view class="dimension-bar">
						<view 
							class="dimension-fill" 
							:style="{ width: dimension.percent + '%' }"
							:class="getDimensionColor(dimension.percent)"
						></view>
					</view>
					<text class="dimension-desc">{{ dimension.description }}</text>
				</view>
			</view>
		</view>
		
		<!-- Âª∫ËÆÆÊåáÂØº -->
		<view class="recommendations-section">
			<view class="section-title">
				<text class="title-text">Âª∫ËÆÆÊåáÂØº</text>
			</view>
			
			<view class="recommendations-list">
				<view 
					class="recommendation-item" 
					v-for="(recommendation, index) in assessmentResult.recommendations" 
					:key="index"
				>
					<view class="recommendation-icon">
						<text class="icon-text">üí°</text>
					</view>
					<text class="recommendation-text">{{ recommendation }}</text>
				</view>
			</view>
		</view>
		
		<!-- ËØÑ‰º∞ËØ¶ÊÉÖ -->
		<view class="details-section">
			<view class="section-title">
				<text class="title-text">ËØÑ‰º∞ËØ¶ÊÉÖ</text>
			</view>
			
			<view class="details-card">
				<view class="detail-item">
					<text class="detail-label">ËØÑ‰º∞Êó•Êúü</text>
					<text class="detail-value">{{ formatDate(assessmentResult.assessmentDate) }}</text>
				</view>
				<view class="detail-item">
					<text class="detail-label">ÂÑøÁ´•Âπ¥ÈæÑ</text>
					<text class="detail-value">{{ calculatedAge }}</text>
				</view>
				<view class="detail-item">
					<text class="detail-label">ÊÄªÈ¢òÊï∞</text>
					<text class="detail-value">{{ assessmentResult.answers.length }} È¢ò</text>
				</view>
				<view class="detail-item">
					<text class="detail-label">ÊÄªÂæóÂàÜ</text>
					<text class="detail-value">{{ assessmentResult.totalScore }}/{{ assessmentResult.maxScore }}</text>
				</view>
			</view>
		</view>
		
		<!-- Â∫ïÈÉ®Êìç‰ΩúÊåâÈíÆ -->
		<view class="action-buttons">
			<button class="action-button secondary" @click="viewHistory">
				<text class="button-text">Êü•ÁúãÂéÜÂè≤</text>
			</button>
			<button class="action-button primary" @click="startNewAssessment">
				<text class="button-text">ÈáçÊñ∞ËØÑ‰º∞</text>
			</button>
		</view>
		
		<!-- ÂÖçË¥£Â£∞Êòé -->
		<view class="disclaimer">
			<text class="disclaimer-text">
				Êú¨ËØÑ‰º∞ÁªìÊûú‰ªÖ‰æõÂèÇËÄÉÔºå‰∏çËÉΩÊõø‰ª£‰∏ì‰∏öÂåªÁñóËØäÊñ≠„ÄÇÂ¶ÇÊúâÁñëÈóÆÔºåËØ∑Âí®ËØ¢‰∏ì‰∏öÂåªÁîü„ÄÇ
			</text>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				assessmentResult: {},
				childInfo: {},
				dimensionScores: []
			}
		},
		computed: {
			calculatedAge() {
				if (!this.childInfo.birthDate) return ''
				const birth = new Date(this.childInfo.birthDate)
				const today = new Date()
				let age = today.getFullYear() - birth.getFullYear()
				const monthDiff = today.getMonth() - birth.getMonth()
				
				if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birth.getDate())) {
					age--
				}
				
				if (age < 1) {
					const months = Math.floor((today - birth) / (1000 * 60 * 60 * 24 * 30))
					return `${months}‰∏™Êúà`
				}
				
				return `${age}Â≤Å`
			}
		},
		onLoad() {
			// Ëé∑ÂèñËØÑ‰º∞ÁªìÊûú
			this.assessmentResult = uni.getStorageSync('assessmentResult') || {}
			this.childInfo = this.assessmentResult.childInfo || {}
			
			// Â¶ÇÊûúÊ≤°ÊúâËØÑ‰º∞ÁªìÊûúÔºåËøîÂõûÈ¶ñÈ°µ
			if (!this.assessmentResult.answers) {
				uni.showToast({
					title: 'ÊöÇÊó†ËØÑ‰º∞ÁªìÊûú',
					icon: 'none'
				})
				setTimeout(() => {
					uni.reLaunch({
						url: '/pages/index/index'
					})
				}, 1500)
				return
			}
			
			// ËÆ°ÁÆóÂêÑÁª¥Â∫¶ÂæóÂàÜ
			this.calculateDimensionScores()
		},
		methods: {
			// ËÆ°ÁÆóÂêÑÁª¥Â∫¶ÂæóÂàÜ
			calculateDimensionScores() {
				const dimensions = {
					'Â§ßËøêÂä®': { score: 0, maxScore: 0, questions: [] },
					'Á≤æÁªÜÂä®‰Ωú': { score: 0, maxScore: 0, questions: [] },
					'ËØ≠Ë®ÄÁêÜËß£': { score: 0, maxScore: 0, questions: [] },
					'ËØ≠Ë®ÄË°®Ëææ': { score: 0, maxScore: 0, questions: [] },
					'Á§æ‰∫§Ë°å‰∏∫': { score: 0, maxScore: 0, questions: [] },
					'ËÆ§Áü•ËÉΩÂäõ': { score: 0, maxScore: 0, questions: [] },
					'Ëá™ÁêÜËÉΩÂäõ': { score: 0, maxScore: 0, questions: [] },
					'ÊÉÖÁª™Ë∞ÉËäÇ': { score: 0, maxScore: 0, questions: [] }
				}
				
				// Ê®°ÊãüÂêÑÁª¥Â∫¶ÁöÑÈóÆÈ¢òÂàÜÈÖçÔºàÂÆûÈôÖÂ∫îËØ•Ê†πÊçÆÈóÆÈ¢òIDÊò†Â∞ÑÔºâ
				const questionMapping = {
					1: 'Â§ßËøêÂä®',
					2: 'Á≤æÁªÜÂä®‰Ωú', 
					3: 'ËØ≠Ë®ÄÁêÜËß£',
					4: 'ËØ≠Ë®ÄË°®Ëææ',
					5: 'Á§æ‰∫§Ë°å‰∏∫',
					6: 'ËÆ§Áü•ËÉΩÂäõ',
					7: 'Ëá™ÁêÜËÉΩÂäõ',
					8: 'ÊÉÖÁª™Ë∞ÉËäÇ'
				}
				
				this.assessmentResult.answers.forEach(answer => {
					const dimension = questionMapping[answer.questionId]
					if (dimensions[dimension]) {
						dimensions[dimension].score += answer.score
						dimensions[dimension].maxScore += 3
						dimensions[dimension].questions.push(answer)
					}
				})
				
				// ËΩ¨Êç¢‰∏∫ÊòæÁ§∫Ê†ºÂºè
				this.dimensionScores = Object.keys(dimensions).map(name => {
					const dim = dimensions[name]
					const percent = Math.round((dim.score / dim.maxScore) * 100)
					return {
						name: name,
						score: dim.score,
						maxScore: dim.maxScore,
						percent: percent,
						description: this.getDimensionDescription(name, percent)
					}
				})
			},
			
			// Ëé∑ÂèñÁª¥Â∫¶ÊèèËø∞
			getDimensionDescription(dimension, percent) {
				if (percent >= 80) {
					return `${dimension}ÂèëËÇ≤ËâØÂ•ΩÔºåÁªßÁª≠‰øùÊåÅ`
				} else if (percent >= 60) {
					return `${dimension}ÂèëËÇ≤Ê≠£Â∏∏ÔºåÊúâÊèêÂçáÁ©∫Èó¥`
				} else {
					return `${dimension}ÈúÄË¶ÅÈáçÁÇπÂÖ≥Ê≥®ÂíåËÆ≠ÁªÉ`
				}
			},
			
			// Ëé∑ÂèñËØÑÂàÜÁ≠âÁ∫ß
			getScoreLevel(score) {
				if (score >= 80) return 'ÂèëËÇ≤ËâØÂ•Ω'
				if (score >= 60) return 'ÂèëËÇ≤Ê≠£Â∏∏'
				return 'ÈúÄË¶ÅÂÖ≥Ê≥®'
			},
			
			// Ëé∑ÂèñËØÑÂàÜÊèèËø∞
			getScoreDescription(score) {
				if (score >= 80) return 'Â≠©Â≠êÁöÑÂêÑÈ°πÂèëËÇ≤ÊåáÊ†áÈÉΩËææÂà∞‰∫ÜÁõ∏Â∫îÂπ¥ÈæÑÊÆµÁöÑÊ≠£Â∏∏Ê∞¥Âπ≥'
				if (score >= 60) return 'Â≠©Â≠êÁöÑÂü∫Êú¨ÂèëËÇ≤Ê≠£Â∏∏ÔºåÈÉ®ÂàÜÊñπÈù¢ËøòÊúâÊèêÂçáÁ©∫Èó¥'
				return 'Âª∫ËÆÆÂèäÊó∂ÂÖ≥Ê≥®Â≠©Â≠êÁöÑÂèëËÇ≤ÊÉÖÂÜµÔºåÂøÖË¶ÅÊó∂ÂØªÊ±Ç‰∏ì‰∏öÊåáÂØº'
			},
			
			// Ëé∑ÂèñÁª¥Â∫¶È¢úËâ≤
			getDimensionColor(percent) {
				if (percent >= 80) return 'color-good'
				if (percent >= 60) return 'color-normal'
				return 'color-attention'
			},
			
			// Ê†ºÂºèÂåñÊó•Êúü
			formatDate(dateString) {
				const date = new Date(dateString)
				return `${date.getFullYear()}Âπ¥${date.getMonth() + 1}Êúà${date.getDate()}Êó•`
			},
			
			// Êü•ÁúãÂéÜÂè≤ËÆ∞ÂΩï
			viewHistory() {
				uni.navigateTo({
					url: '/pages/history/history'
				})
			},
			
			// ÈáçÊñ∞ËØÑ‰º∞
			startNewAssessment() {
				uni.showModal({
					title: 'ÈáçÊñ∞ËØÑ‰º∞',
					content: 'Á°ÆÂÆöË¶ÅÂºÄÂßãÊñ∞ÁöÑËØÑ‰º∞ÂêóÔºü',
					success: (res) => {
						if (res.confirm) {
							uni.reLaunch({
								url: '/pages/index/index'
							})
						}
					}
				})
			}
		}
	}
</script>

